<template>
  <div class="session-container">
    <h2 class="title">📅 Upcoming Sessions</h2>

    <!-- Session Cards -->
    <div class="session-grid">
      <div v-for="session in sessions" :key="session.id" class="session-card">
        <div class="session-info">
          <h3 class="session-topic">🎓 {{ session.topic }}</h3>
          <p class="session-time">🕒 {{ formatDateTime(session.time) }}</p>
          <p class="session-link">
            🔗 <a :href="session.link" target="_blank">{{ session.link }}</a>
          </p>
        </div>
        <button class="join-btn" @click="joinSession(session)">Join Session</button>
      </div>
    </div>

    <!-- Create New Session -->
    <div class="create-session-form">
      <h3 class="form-title">➕ Create New Session</h3>
      <form @submit.prevent="createSession">
        <div class="form-group">
          <label class="form-label">Topic</label>
          <input
            v-model="topic"
            type="text"
            placeholder="Enter session topic"
            required
            class="form-input"
          />
        </div>
        <div class="form-group">
          <label class="form-label">Time</label>
          <input
            v-model="time"
            type="datetime-local"
            required
            class="form-input"
          />
        </div>
        <div class="form-group">
          <label class="form-label">Session Link (Zoom / Jitsi / WebRTC)</label>
          <input
            v-model="link"
            type="url"
            placeholder="https://meet.jit.si/your-room"
            required
            class="form-input"
          />
        </div>
        <button type="submit" class="submit-btn">Create Session</button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      topic: '',
      time: '',
      link: '',
      sessions: []
    };
  },
  mounted() {
    this.fetchSessions();
  },
  methods: {
    async fetchSessions() {
      try {
        const res = await fetch('/session/all');
        if (res.ok) {
          this.sessions = await res.json();
        } else {
          console.error("❌ Failed to fetch sessions");
        }
      } catch (err) {
        console.error("❌ Error fetching sessions", err);
      }
    },
    async createSession() {
      try {
        const token = localStorage.getItem('token');
        if (!token) {
          alert('❌ You must be logged in to create a session');
          return;
        }

        const response = await fetch('/session/create', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${token}`
          },
          body: JSON.stringify({
            topic: this.topic,
            time: this.time,
            link: this.link
          })
        });

        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(errorData.error || 'Unknown error');
        }

        const result = await response.json();
        this.sessions.push(result); // Show instantly

        alert(`✅ Session created: "${result.topic}"`);

        // Clear form
        this.topic = '';
        this.time = '';
        this.link = '';
      } catch (err) {
        alert(`❌ Error: ${err.message}`);
        console.error(err);
      }
    },
    joinSession(session) {
      if (session.link) {
        window.open(session.link, '_blank');
      } else {
        alert("❌ No session link provided.");
      }
    },
    formatDateTime(datetime) {
      return new Date(datetime).toLocaleString();
    }
  }
};
</script>

<style scoped>
.session-container {
  max-width: 800px;
  margin: 2rem auto;
  padding: 1rem;
}

.title {
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
  background: linear-gradient(to right, #3b82f6, #6366f1);
  color: white;
  padding: 0.8rem;
  border-radius: 1rem;
  margin-bottom: 2rem;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.session-grid {
  display: grid;
  gap: 1.5rem;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  margin-bottom: 2.5rem;
}

.session-card {
  background-color: #fff;
  border: 2px solid #e0e7ff;
  border-radius: 1rem;
  padding: 1.5rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.07);
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.session-card:hover {
  transform: translateY(-4px);
  border-color: #6366f1;
  box-shadow: 0 6px 20px rgba(99, 102, 241, 0.2);
}

.session-info {
  margin-bottom: 1rem;
}

.session-topic {
  font-size: 1.25rem;
  font-weight: 600;
  color: #111827;
}

.session-time {
  font-size: 0.95rem;
  color: #6b7280;
  margin-bottom: 1rem;
}

.session-link a {
  color: #3b82f6;
  word-break: break-word;
  font-size: 0.9rem;
}

.join-btn {
  background-color: #10b981;
  color: white;
  padding: 0.5rem 1rem;
  font-weight: 600;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background 0.2s ease;
}

.join-btn:hover {
  background-color: #059669;
}

.create-session-form {
  background: linear-gradient(to bottom right, #f9fafb, #e0f2fe);
  padding: 2rem;
  border-radius: 1.25rem;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.08);
  border: 1px solid #d1d5db;
}

.form-title {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 1.5rem;
  color: #1d4ed8;
  text-align: center;
}

.form-group {
  margin-bottom: 1.2rem;
}

.form-label {
  display: block;
  margin-bottom: 0.4rem;
  font-weight: 500;
  color: #374151;
}

.form-input {
  width: 100%;
  padding: 0.6rem 0.8rem;
  border: 1px solid #cbd5e1;
  border-radius: 0.6rem;
  font-size: 1rem;
  outline: none;
  transition: border 0.2s ease;
}

.form-input:focus {
  border-color: #3b82f6;
  box-shadow: 0 0 0 2px rgba(59, 130, 246, 0.3);
}

.submit-btn {
  background-color: #2563eb;
  color: white;
  padding: 0.75rem 1rem;
  font-weight: bold;
  border: none;
  border-radius: 0.5rem;
  width: 100%;
  cursor: pointer;
  transition: background 0.2s ease;
}

.submit-btn:hover {
  background-color: #1e40af;
}
</style>
